/*
====================================
== Typography
====================================
*/
h1, h2, h3, h4, h5 {
  text-rendering: optimizeLegibility; }

h1 {
  font-size: 3.157rem;
  line-height: 90px;
  font-weight: 700; }

h2 {
  font-size: 2.369rem;
  line-height: 45px;
  font-weight: 300;
  letter-spacing: 2px; }

h3 {
  font-size: 1.778rem;
  line-height: 45px;
  font-weight: 400;
  letter-spacing: 1px; }

h4 {
  font-size: 1.333rem;
  line-height: 22.5px;
  font-weight: 700;
  letter-spacing: 0.5px;
  opacity: 0.87; }

p {
  font-size: 1rem;
  line-height: 22.5px;
  font-weight: 400;
