/*
====================================
== General globals and sections
====================================
*/
html {
  font-size: 18px;
  background-color: #ddebed; }

header {
  background-color: #0a0a0a;
  padding: 22.5px 0 22.5px 0; }

footer {
  background-color: #0a0a0a;
  color: #4d4d4d;
  height: auto;
  padding: 22.5px 0 22.5px 0; }

section {
  padding: 18px 5%;
  width: 100%; }
  @media (min-width: 601px) {
    section {
      padding: 18px 8%; } }
  @media (min-width: 901px) {
    section {
      padding: 18px 15%; } }
  section::after {
    content: '';
    display: table;
    clear: both; }

.section__light {
  background-color: #ddebed;
  color: #0a0a0a; }

.section__dark {
  background-color: #4d4d4d;
  color: #fcfcfc; }

/*
====================================
== Header and Navigation
====================================
*/
.logo {
  flex: 2 auto; }
  .logo img {
    height: 45px; }

nav {
  flex: 1; }
  nav ul {
    list-style: none;
    display: flex;
    justify-content: space-between; }
  nav a {
    font-size: 1.333rem;
    line-height: 22.5px;
    font-weight: 400;
    text-decoration: none;
