/*
====================================
== Typography
====================================
*/

h1, h2, h3, h4, h5 {
  text-rendering: optimizeLegibility;
  // margin: 0;
}
h1 {
  @include type-setting(4);
  font-weight: 700;
}
h2 {
  @include type-setting(3);
  font-weight: 300;
  letter-spacing: 2px;
}
h3 {
  @include type-setting(2);
  font-weight: 400;
  letter-spacing: 1px;
}
h4 {
  @include type-setting(1);
  font-weight: 700;
  letter-spacing: 0.5px;
  opacity: 0.87;
}
p {
  @include type-setting(0);
  font-weight: 400;
  opacity: 0.87;
}
h5 {
  @include type-setting(-1);
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
}
